/* 🔹 Contact List Container */
.contact-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.contact-list {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* แสดง 3 คนต่อแถว */
  gap: 20px;
  max-width: 900px;
  margin: auto;
  place-items: center;
}
.contact-title {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30px;
  padding-bottom: 30px;
}
.contact-h4 {
  font-size: 24px;
}
/* 🔹 Contact Item (รูปแบบสี่เหลี่ยม) */
.contact-item {
  background: #fff;
  padding: 20px;
  text-align: center;
  border-radius: 10px;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: black;
  height: 236px;
  width: 240px;
}

/* 🔹 รูปภาพเป็นสี่เหลี่ยมจัตุรัส */
.contact-image {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: 10px;
  margin-bottom: 10px;
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.1);
}

/* 🔹 ชื่อ */
.contact-name {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 10px;
}

/* 🔹 Social Icons */
.contact-socials {
  display: flex;
  gap: 10px;
}

.contact-socials a {
  color: #555;
  font-size: 20px;
  transition: color 0.3s;
}

.contact-socials a:hover {
  color: #b20710;
}
.contact-divider {
  width: 80%;
  height: 2px;
  background-color: white; /* เปลี่ยนสีเป็นขาว */
  margin: 40px auto; /* ตั้ง margin auto ทำให้กึ่งกลาง */
  opacity: 0.5;
}

@media (max-width: 1025px) {
  .contact-list {
    grid-template-columns: repeat(2, 1fr); /* 2 คนต่อแถว */
  }
  .contact-item {
    height: 236px;
    width: 180px;
  }
}

@media (max-width: 480px) {
  .contact-list {
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
  }
  .contact-item {
    height: 236px;
    width: 130px;
  }
}
